/**
* button_fancy.module.css
*/
.button_fancy {
  --perX: 50%;
  --perY: 50%;
  font-family: "Play", sans-serif;
  letter-spacing: 0.05em;
  position: relative;
  color: #78a;
  text-decoration: none;
  padding: 1em 2.5em;
  font-size: 2em;
  transition: color 0.5s, background-color 0.5s, box-shadow 0.5s, transform 0.1s;
  background-color: #234;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0)), radial-gradient(100% 2.5% at 50% 0%, rgba(34, 102, 255, 0.2), rgba(34, 102, 255, 0)), radial-gradient(100% 2.5% at 50% 100%, rgba(34, 102, 255, 0.2), rgba(34, 102, 255, 0));
  overflow: hidden;
}
.button_fancy::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s;
  background-image: radial-gradient(50% 80% at var(--perX) 0%, rgba(34, 102, 255, 0.3), rgba(34, 102, 255, 0)), radial-gradient(50% 80% at var(--perX) 100%, rgba(34, 102, 255, 0.3), rgba(34, 102, 255, 0)), radial-gradient(40% 200% at var(--perX) 50%, rgba(34, 102, 255, 0.2), rgba(34, 102, 255, 0)), radial-gradient(50% 2% at var(--perX) 0%, #2266ff, rgba(34, 102, 255, 0)), radial-gradient(50% 2% at var(--perX) 100%, #2266ff, rgba(34, 102, 255, 0)), radial-gradient(50% 2% at var(--perX) 0%, #2266ff, rgba(34, 102, 255, 0)), radial-gradient(50% 2% at var(--perX) 100%, #2266ff, rgba(34, 102, 255, 0));
  mix-blend-mode: screen;
}
.button_fancy:hover {
  color: #acf;
  background-color: #236;
  box-shadow: inset rgba(34, 102, 255, 0.2) 0px 0px 0.6em 1px, rgba(34, 102, 255, 0.2) 0px 0px 1.2em 3px;
}
.button_fancy:hover::before {
  opacity: 1;
}
.button_fancy:active {
  transform: translateY(2.5%);
}